<template>
    <div class="cart-container container mt-5">
        <h2 class="text-center mb-4"></h2>
        <div v-if="cartItems.length === 0" class="text-center">
            <p></p>
        </div>
        <template v-else>
            <table class="table table-hover">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, index) in cartItems" :key="item.id">
                        <!--  -->
                        <td>
                            <img :src="item.image" alt="" class="product-image"/>
                        </td>
                        <td>{{ item.name }}</td>
                        <td>{{ item.price }}</td>
                        <!-- input -->
                        <td>
                            <input type="number" class="form-control quantity-input" 
                            v-model.number="item.quantity" 
                            @input="updateQuantity(index,item.quantity)" min="1" />
                        </td>
                        <td>{{ item.quantity * item.price }}</td>
                        <td>
                            <button class="btn btn-danger" @click="removeItem(index)"></button>
                        </td>
                    </tr>
                </tbody>
            </table>
            <div class="d-flex justify-content-between align-items-center">
                <h4>{{ totalAmount }}</h4>
                <button class="btn btn-success" @click="checkout"></button>
            </div>
        </template>
    </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex';
import axios from 'axios';

    export default {
    name: 'ShopCart',
    computed:{
        ...mapGetters('cart',['cartItems','cartTotal'])
    },
    methods:{
        ...mapActions('cart',{
            removeItemFromCart:'removeItemFromCart',
            updateQuantityInCart:'updateQuantityInCart'
        }),
        //
        updateQuantity(index,quantity){
            this.updateQuantity({index,quantity});
        },
        // 
        removeItem(index){
            this.cartItemFromCart(index);
        },
        // 
        async checkouot(){
            if(this.cartItems.length === 0){
                alert('');
            }
            try{
                const response = await axios.post('/api/orders',{
                    items:this.cartItems,
                    total:this.cartTotal
                });
                console.log('',response.data);
                alert(` ${this.totalAmount},`);
            }catch(error){
                console.error('',error);
                alert('');
            }
        }
    } ,
};   
</script>

