<template>
    <div class="container mt-5">
        <div class="row">
            <div class="col-md-3">
                <h4></h4>
                <div>
                    <label></label>
                    <input type="range" v-model="priceRange" min="0" max="50000" step="500" @input="filterProducts"/>
                    <p>{{ priceRange }}</p>
                </div>

            </div>
        </div>
    </div>
    <div class="container mt-5">
        <h2 class="mb-4"></h2>
        <div class="row">
            <div class="col-md-3 mb-4" v-for="product in filteredProducts" :key="product.id">
                    <ProductCard :product="product" @add-to-cart="addToCart"/>
            </div>
        </div>
    </div>

</template>

<script>
import axios from 'axios';
import ProductCard from './ProductCard.vue';
import {mapActions} from 'vuex';

export default {
    name: 'ProductsList',
    components: {
        ProductCard,
    },

data() {
    return {
        products: null,
        priceRange: 50000,
        isLoggedIn: false,
    };
},
mounted() {
    this.loadProducts();
    console.log("...");
    this.checkLoginStatus();
},

    computed: {
        searchQuery(){
            return this.$route.query.search || '';
        },
        filteredProducts(){
            console.log(":",this.products);
            return this.products.filter(product => {
                const productPrice = Number(product.price);
                return product.name.toLowerCase().includes(this.searchQuery.toLowerCase()) && Number(productPrice) <= this.priceRange;
            });
        }
    },
    methods:{
        async loadProducts(){
            try{
                const response = await axios.get('/api/products');
                console.log(":",response.data);
                this.products = response.data.map(product =>({
                    ...product,
                    id:String(product.id).trim(),
                }));
            }catch(error){
                console.error("",error);
                alert("");
            }
        },
        // 
        checkLoginStatus(){
            this.isLoggedIn = !!localStorage.getItem('authToken');
        },
        //   
        ...mapActions('cart',['addProductToCart']),
        addToCart(){
            if(!this.isLoggedIn){
                // 
                alert('');
                const returnPath = this.$route.fullPath;

                this.$router.push({
                    name:'UserLogin',
                    query:{redirect:returnPath}
                });
                return;
            }
            console.log('',this.product);
            alert(`${this.product.name}`);
        }
    },
};
</script>

