<template>
    <div class="container mt-5">
        <div v-if="product && product.id">
            <div class="product-detail">
                <img :src="productImageUrl" alt="Product Image" class="product-image-large">
                <h2>{{ product.name }}</h2>
                <p>{{ product.description }}</p>
                <p>{{ product.price }} </p>
                <button class="btn btn-primary" @click="addToCart"></button>
            </div>
        </div>
        <div v-else>
            <p>...</p>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { mapActions } from 'vuex';

export default {
    name: 'ProductDetail',
    data() {
        return {
            product: null,
            isLoggedIn: false,
        };
    },
    computed:{
        productId(){
            return this.$route.params.id;
        },
        productImageUrl(){
            return this.product ? `/api/products/${this.productId.image}` : '';
        }
    },
    mounted() {
        this.fetchProduct();
        this.checkLoginStatus();
    },
    methods: {
        async fetchProduct() {
            try {
                const response = await axios.get(`/api/products/${this.productId}`);
                console.log("API ", response.data);
                this.product = response.data;
            } catch (error) {
                console.error("", error);
                alert("");
            }
        },
        checkLoginStatus() {
            this.isLoggedIn = !!localStorage.getItem('authToken');
        },        
        ...mapActions('cart',['addProductToCart']),
        addToCart() {
            if (!this.isLoggedIn) {
                alert('');
                const returnPath = this.$route.fullPath;
                this.$router.push({
                    name:'UserLogin',
                    query:{ redirect: returnPath}
                });
                return;
            }
            this.addProductToCart(this.product);
            alert(`${this.product.name}`);
        },
    },
};
</script>

<style scoped>
.product-detail {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.product-image-large {
    width: 300px;
    height: 300px;
    object-fit: cover;
    margin-bottom: 20px;
}
</style>
